.offerings {
  @extend %section-true;
  box-sizing: border-box;
  @extend %section;
  background-size: cover;
  padding-top: 41px;
  padding-bottom: 60px;
  background-image: url(../images/offerings-bg-phone.jpg);
 

  @media screen and (min-width: 480px) {
    // width: 480px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 150px;
    padding-bottom: 100px;
    background-image: url(../images/offerings-bg-tablet.jpg);
    // width: 768px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 177px;
    padding-bottom: 43px;
    // width: 1280px;
    background-image: url(../images/Offerings-bg-desc.jpg);
  }
}

.offerings__container {
  @media screen and (min-width: 1280px) {
    display: flex;
    align-items: center;
  }
}

.offerings__image {
  margin-bottom: 60px;

  @media screen and (min-width: 1280px) {
    margin-bottom: 0;
  }
}

.offerings__uniq {
  @media screen and (min-width: 1280px) {
    margin-left: 90px;
  }
}

.offerings__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 1.17;
  color: $primary-text-color;
  margin-bottom: 28px;

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;

    max-width: 526px;
    font-size: 42px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 42px;
    line-height: 1.2;
  }
}

.offerings__subtitle {
  margin-bottom: 28px;
  font-size: 14px;
  line-height: 1.17;
  color: $primary-text-color;

  @media screen and (min-width: 768px) {
    max-width: 500px;
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 1.19;
  }

  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: 1.5;
  }
}

.offerings__list {
  padding-left: 0;

  @media screen and (min-width: 768px) {
    display: flex;
    gap: 116px;
  }

  @media screen and (min-width: 1280px) {
    gap: 67px;
  }
}

.offerings__item {
  @media screen and (min-width: 768px) {
    width: 252px;
  }
}

.offerings__i {
  width: 48px;
  height: 48px;
  background-color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.offerings__icon {
  width: 32px;
  height: 32px;
}

.offerings__article {
  margin-top: 8px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.17;

  color: $primary-text-color;

  @media screen and (min-width: 1280px) {
    line-height: 1.4;
  }
}

.offerings__description {
  margin-top: 16px;
  margin-bottom: 28px;
  font-size: 14px;
  line-height: 1.14;
  color: $primary-text-color;

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }

  @media screen and (min-width: 1280px) {
    margin-bottom: 32px;
    line-height: 1.5;
  }
}

.offerings__order {
  display: flex;
  // gap: 24px;
  margin-right: 24px;
  align-items: center;
}

.offerings__button {
  width: 106px;
  height: 40px;
  margin-right: 24px;
  background: $picture-background-color;
  border: 2px solid $accent-color-dark;
  border-radius: 30px;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.5;
  transition: border-color $animation;

  &:hover
  &:focus {
    border-color: #597d00;
  }
}

.offerings__text {
  margin-right: 4px;
  font-size: 13.67px;
  line-height: 1.5;
  color: $secondary-text-color;



  @media screen and (min-width: 1280px) {
    display: none;
  }
}

.offerings__arrow{
  width: 13.67px;
  height: 13.67px;

  @media screen and (min-width: 1280px) {
      display: none;
    }
}